body {
  min-width: 360px;
  margin: 0;
  padding: 0;
  font-size: 17px;
  line-height: 25px;
  font-family: "Raleway", sans-serif;
  color: #ffffff;
  background-color: #1c1c1c; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

@media (max-width: 1366px) {
  .page-header {
    padding: 24px 39px 0 36px; }
  .page-header__toggle {
    display: none; }
  .page-header__container {
    display: flex;
    justify-content: space-between;
    align-items: center; } }

@media (max-width: 360px) {
  .page-header__container {
    flex-direction: column; }
  .page-header__toggle {
    border: none;
    margin: 0;
    padding: 0;
    font-weight: 600;
    font-size: 14px;
    line-height: 16px;
    text-transform: uppercase;
    background-color: transparent;
    color: #f0f0f0;
    display: flex;
    align-items: center;
    cursor: pointer;
    width: 75px;
    justify-content: space-between; }
  .page-header__wrapper {
    padding: 20px 17px 0 18px;
    display: flex;
    justify-content: space-between;
    width: 100%; }
  .page-header__close-toggle {
    position: relative;
    display: block;
    width: 15px;
    height: 15px; }
  .page-header__close-toggle::before,
  .page-header__close-toggle::after {
    position: absolute;
    content: "";
    width: 20px;
    height: 2px;
    background-color: #ffffff;
    top: 6px;
    right: -2px; }
  .page-header__close-toggle::before {
    transform: rotate(45deg); }
  .page-header__close-toggle::after {
    transform: rotate(-45deg); }
  .page-header {
    flex-direction: column;
    padding: 0; }
    .page-header--menu-closed .page-header__close-toggle {
      width: 16px;
      height: 2px;
      background-color: #ffffff;
      margin-right: 8px;
      justify-content: flex-start; }
    .page-header--menu-closed .page-header__close-toggle::before,
    .page-header--menu-closed .page-header__close-toggle::after {
      transform: none;
      right: -4px; }
    .page-header--menu-closed .page-header__close-toggle::before {
      top: -6px; }
    .page-header--menu-closed .main-nav {
      display: none; }
    .page-header--menu-opened {
      min-height: 65px; }
      .page-header--menu-opened .main-nav {
        display: flex;
        z-index: 20; }
      .page-header--menu-opened .page-header__container {
        position: absolute;
        top: 0;
        left: 0;
        background-color: #1c1c1c; }
      .page-header--menu-opened .page-header {
        position: relative; } }

@media (max-width: 1366px) {
